import React, { useState } from 'react';
import Images from 'next/image'


const Sidebar = () => {
  const [isOpen, setIsOpen] = useState(true);

  return (
    <>
      {/* â˜° Open Button â€“ only on small screens */}
      {!isOpen && (
        <div
          className="p-2 bg-[#340260] text-white fixed top-4 left-4 z-50 cursor-pointer rounded lg:hidden"
          onClick={() => setIsOpen(true)}
        >
          â˜°
        </div>
      )}

      {/* Sidebar */}
      <div
        className={`fixed top-0 left-0 h-screen bg-[#340260] text-white w-64 p-6 transform transition-transform duration-300 z-40
        ${isOpen ? 'translate-x-0' : '-translate-x-full'} 
        lg:translate-x-0 lg:static lg:block`}
      >
        {/* âœ• Close Button â€“ only on small screens */}
        <div
          className="text-white text-xl cursor-pointer absolute top-4 right-4 lg:hidden"
          onClick={() => setIsOpen(false)}
        >
          âœ•
        </div>

        <h2 className="text-2xl font-bold mb-6">Hello</h2>

        <nav className="flex flex-col space-y-4">
  <a href="#" className="flex items-center gap-2 hover:text-purple-300">
    <Image src="/icons/dashboard.svg" alt="Dashboard" width={16} height={16} />
    Dashboard
  </a>
  <a href="#" className="flex items-center gap-2 hover:text-purple-300">
    <Image src="/icons/programs.svg" alt="Programs" width={16} height={16} />
    Programs
  </a>
  <a href="#" className="flex items-center gap-2 hover:text-purple-300">
    <Image src="/icons/users.svg" alt="Users" width={16} height={16} />
    Users
  </a>
  <a href="#" className="flex items-center gap-2 hover:text-purple-300">
    <Image src="/icons/settings.svg" alt="Settings" width={16} height={16} />
    Settings
  </a>
  <a href="#" className="flex items-center gap-2 hover:text-purple-300">
    <Image src="/icons/logout.svg" alt="Logout" width={16} height={16} />
    Logout
  </a>
</nav>




      </div>
    </>
  );
};

export default Sidebar;
